<template>
	<div class="PutawayDetails">
		<header><i class="iconfont icon-xiangzuo1" @click="sendParams"></i><span>上架库存</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span>库位代码</span>
					<input v-model="Loc" readonly></input>
				</div>
				<div>
					<span>托盘号</span>
					<input v-model="PID" readonly></input>
				</div>
			</div>
			<div>
				<div>
					<span>商品代码</span>
					<input v-model="Sku" readonly></input>
				</div>
				<div>
					<span>客户代码</span>
					<input v-model="StorerKey" readonly></input>
				</div>
			</div>

			<div class="goodsName">
				<span>商品名称</span>
				<textarea v-model="Descr" readonly></textarea>
			</div>
			<div>
				<div>
					<span>系统数量</span>
					<input v-model="Qty" readonly></input>
				</div>
				<div>
					<span>单位</span>
					<input v-model="Susr2" readonly></input>
				</div>
			</div>
			<div>
				<div>
					<span>推荐库位</span>
					<input v-model="SuggestLoc" readonly></input>
				</div>
			</div>
			<div>
				<div>
					<span>确认库位</span>
					<input @focus="g_focus('ToLoc')" @blur="g_blur()"   @change="toUppercase('ToLoc')" ref="ToLoc" placeholder="请输入内容" v-model="ToLoc" class="writable" @keyup.enter="confirmOnclick" autofocus="autofocus"></input>
				</div>
				<div>
					<span>上架数量</span>
					<input ref="PutQty"  @focus="g_focus('PutQty')"   placeholder="请输入内容"   @change="toUppercase('PutQty')"  type="number" v-model="PutQty" class="writable" @keyup.enter="confirmOnclick"></input>
				</div>
			</div>

			<span class="g_singleBtn" @click="confirmOnclick">确认</span>
		</div>

		<div v-if="tipsHide"  @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">
	export default{
		name:"PutawayDetails",
		data(){
			return{
				Loc:'',
				PID:'',
				Sku:'',
				StorerKey:'',
				Descr:'',
				Qty:'',
				Susr2:'',
				SuggestLoc:'',
				ToLoc:'',
				PutQty:'',
				data:[],
				tipsHide:false, //提示信息是否显示
				msg:'',     //提示信息
				inputFocus:"",
			}
		},
		mounted() {

			this.$refs.ToLoc.focus();

			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		},
		created(){

			var data = this.$route.query.listData.data[0];
			this.data = data;
			this.Loc = data.loc;
			this.PID = data.pid;
			this.Sku = data.sku;
			this.StorerKey = data.storerkey;
			this.Descr = data.descr;
			this.Qty = data.qty;
			this.Susr2 = data.susr2;
			this.SuggestLoc = data.suggestloc;
			this.PutQty = data.qty;
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				
				this[inputName] = qrcode;

				this.confirmOnclick();
				
			},
			confirmOnclick(){

				var that = this;

				if(this.ToLoc == ''){

					this.msg = "请确认库位";
					that.tipsShow("ToLoc");
					that.inputFocus = "ToLoc";
				}else{

					if(this.PutQty == ''){

						this.msg = "请输入上架数量";
						that.tipsShow("PutQty");
						that.inputFocus = "PutQty";
					}else{

						if(this.PutQty <= 0){

							this.msg = "上架数量不能小于0";
							that.tipsShow("PutQty");
							that.inputFocus = "PutQty";
						}else{
							var currentUrl = this.HOST + "RFPutawayConfirm.do";
							var postData = {
								"Lot":this.data.lot,
								"Loc":this.data.loc,
								"ToLoc":this.ToLoc,
								"PutQty":this.PutQty,
								"StorerKey":this.data.storerkey,
								"Sku":this.data.sku,
								"PID":this.data.pid,
								"SuggestLoc":this.SuggestLoc,
							}

							this.httpRequest(currentUrl,postData,function(res){

								console.log("成功")
								that.$router.push({
									name:"Putaway",
								})
							},"ToLoc")
						}
					}
				}
			},
			sendParams:function(){

				var that = this;

				//返回到上一页（库存查询页面）
				this.$router.push({
					name:"Putaway",
					query:{
						inventoryCondition:that.$store.state.inventoryCondition
					}
				})
			}
		}
	}
</script>
<style scoped lang="less">
.PutawayDetails{
	.container{
		font-size: 30/50rem;
		&>div{
			width:100%;
			height: 55/50rem;
			box-sizing: border-box;
			margin-top: 20/50rem;
			display:inline-block;
			&>div{
				width:50%;
				float: left;

				span{
					width: 130/50rem;
					display:inline-block;
					text-align: right;
				}

				input{
					width:192/50rem;	
					height:55/50rem;
					padding:0 10/50rem;
					background-color: #ccc;
					font-size: 30/50rem;
					margin: 0;
				}
			}

			.writable{
				background-color: #fff;
			}
		}

		.goodsName{
			height:80/50rem;
			&>span{
				width: 130/50rem;
				text-align: right;
				margin-right: 1/50rem;
				float: left;
			}
			&>textarea{
				width:585/50rem;
				height:80/50rem;
				float:left;
				outline:none;
				margin-left: 8/50rem;
				background-color: #ccc;
				font-size: 30/50rem;
			}
		}
		.g_singleBtn{
			margin:40/50rem auto;
		}
	}
}

// 374像素以下（包含374像素）
@media screen and (max-width: 374px){

	.PutawayDetails{
		.container{
			.goodsName{
				&>textarea{
					width:592/50rem;
				}
			}
		}
	}
}

// 277像素以下(包含277像素)
@media screen and (max-width: 277px){
	.PutawayDetails{
		.container{
			font-size: 29/50rem;
			&>div{
				&>div{

					span{
						width: 150/50rem;
					}

					input{
						width:172/50rem;
					}
				}
			}
			.goodsName{
				&>span{
					width: 150/50rem;
				}
				&>textarea{
					width:570/50rem;
				}
			}
		}
	}
}
</style>


